.phonebox{

  align-content: center;
  width: 200px;
  height: 315px;
  border-radius: 1px;
  top: -5px;
  position: relative;
  background-image: url('https://lh3.googleusercontent.com/Jsgvdf1g9pOjkeUc4mQXKwxS6_v9PCj0oYGxpNu7nVwAErty_z_KOsprrly41z0GY64KHMpXBpdfyzFS3i1l=s400');
  //transform: scale3d(1, 1, 1);
 
    animation: swing 1s;
    animation-timing-function: ease-in-out;
   
    
