@keyframes swing{
  0%   {transform: perspective(2000px) translate3d(0, 0px, 0px) rotateX(0deg) scale3d(1, 1, 1) translateY(0px);}
  25%  {transform: perspective(2000px) translate3d(0, -53px, 10px) rotateX(-20deg) scale3d(1, 1, 1) translateY(50px);}
  50%   {transform: perspective(2000px) translate3d(0, 0px, 0px) rotateX(0deg) scale3d(1, 1, 1) translateY(0px);}
  75%  {transform: perspective(2000px) translate3d(0, -59px, 15px) rotateX(20deg) scale3d(1, 1, 1) translateY(50px);}
  100% {transform: perspective(2000px) translate3d(0, 0px, 0px) rotateX(0deg) scale3d(1, 1, 1) translateY(0px);}
}


