.areacart {
  background-image: url('https://www.craftycreations.net/wp-content/uploads/2019/08/Red-Brick-Block-300x300.png');
  background-repeat: repeat;
  width: 500px;
  height: 375px;
  border: none;
  margin-top: 0px;
  top: 0px;
  position: relative;
  font-family: 'Minecraft', sans-serif;
  color: #fff;
  transition: height 1s;
  animation-timing-function: ease-in-out;
  animation-direction: normal;
  animation: appear 2s;
  animation-delay: 0s; 
  z-index: 10;
}
